.jairg__overview {
    cursor: default;
    display: flex;
    flex-direction: row;
}

.jairg__overview-image {
    align-items: center;
    display: flex;
    flex: 1;
    justify-content: flex-start;
    margin-right: 2rem;
}

.jairg__overview-image img {
    height: 40rem;
    width: 100%;
}

.jairg__overview-content {
    align-items: flex-start;
    display: flex;
    flex-direction: column;
    flex: 1;
    justify-content: flex-end;
}

.jairg__overview-content h4 {
    color: #71E5FF;
    font-family: var(--font-family);
    font-size: 16px;
    font-weight: 500;
    line-height: 30px;
}

.jairg__overview-content h1 {
    font-family: var(--font-family);
    font-size: 34px;
    font-weight: 800;
    line-height: 45px;
    margin: 1rem 0;
}

.jairg__overview-content p {
    color: var(--color-text);
    font-family: var(--font-family);
    font-size: 16px;
    font-weight: 400;
    line-height: 30px;
    margin-bottom: 2rem;
    text-align: justify;
}

@media screen and (max-width: 950px) {
    .jairg__overview {
        flex-direction: column-reverse;
    }

    .jairg__overview-image {
        margin: 1rem 0;
    }

    .jairg__overview-image img {
        height: unset;
        width: unset;
    }

    .jairg__overview-content {
        margin-top: 2rem;
    }
}

@media screen and (max-width: 700px) {
    .jairg__overview-image img {
        height: 100%;
        width: 100%;
    }
}

