.jairg__navbar {
    align-items: center;
    display: flex;
    justify-content: space-between;
    padding: 2rem 6rem;
}

.jairg__navbar-links {
    align-items: center;
    display: flex;
    flex: 1;
    justify-content: space-between;
}

.jairg__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.jairg__navbar-links_container p,
.jairg__navbar-menu_container p {
    color: #fff;
    cursor: pointer;
    font-family: var(--font-family);
    font-size: 18px;
    font-weight: 500;
    line-height: 25px;
    margin: 0 1rem;
    text-transform: capitalize;
}

.jairg__navbar-menu_container button {
    background: #FF4820;
    border-radius: 5px;
    border: none;
    color: #fff;
    cursor: pointer;
    font-family: var(--font-family);
    font-size: 18px;
    font-weight: 500;
    line-height: 25px;
    outline: none;
    padding: 0.5rem 1rem;
}

.jairg__navbar-menu {
    display: none;
    margin-left: 1rem;
    position: relative;
}

.jairg__navbar-menu svg {
    cursor: pointer;
}

.jairg__navbar-menu_container {
    align-items: flex-end;
    background: var(--color-footer);
    border-radius: 5px;
    box-shadow: 0px 0px 5px rgba(0,0, 0,0.2);
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    margin-top: 1rem;
    min-width: 200px;
    padding: 2rem;
    position: absolute;
    right: 0;
    text-align: start;
    top: 40px;
}

.jairg__navbar-menu_container p {
    margin: 1rem 0;
}

.link-text {
    transition: .3s ease-in-out;
}

.link-text:hover {
    transform: scale(1.1);
    color: #71E5FF;
}

@media screen and (max-width: 1050px) {
    .jairg__navbar-links_container {
        display: none;
    }

    .jairg__navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 700px) {
    .jairg__navbar {
        padding: 2rem 4rem;
    }
}

@media screen and (max-width: 550px) {
    .jairg__navbar {
        padding: 2rem;
    }

    .jairg__navbar-menu_container {
        top: 20px;
    }
}
