.jairg__header {
    cursor: default;
    display: flex;
    flex-direction: row;
    padding: 1.5rem 6rem 4rem 6rem;
}

.jairg__header-content {
    align-items: flex-start;
    display: flex;
    flex-direction: column;
    flex: 1;
    justify-content: center;
    margin-right: 5rem;
}

.jairg__header-content h1 {
    font-family: var(--font-family);
    font-size: 62px;
    font-weight: 800;
    letter-spacing: -0.04em;
    line-height: 75px;
}

.jairg__header-content p {
    color: var(--color-text);
    font-family: var(--font-family);
    font-size: 20px;
    font-weight: 400;
    line-height: 28px;
    margin-top: 1.5rem;
}

.jairg__header-image {
    align-items: center;
    display: flex;
    flex: 1;
    justify-content: center;
}

.jairg__header-image img {
    height: 100%;
    width: 100%;
}

@media screen and (max-width: 1050px) {
    .jairg__header {
        flex-direction: column;
    }

    .jairg__header-content {
        margin: 0 0 3rem;
    }
}

@media screen and (max-width: 650px) {
    .jairg__header-content h1 {
        font-size: 48px;
        line-height: 60px;
    }

    .jairg__header-content p {
        font-size: 16px;
        line-height: 24px;
    }
}

@media screen and (max-width: 490px) {
    .jairg__header-content h1 {
        font-size: 36px;
        line-height: 48px;
    }

    .jairg__header-content p {
        font-size: 14px;
        line-height: 24px;
    }
}
