.jairg__form {
    align-items: center;
    background: #092040;
    display: flex;
    justify-content: space-between;
    padding: .1rem;
}

.agtc-img {
    align-self: center;
    justify-self: center;
    width: 20rem;
}

.jairg__form-feature {
    display: flex;
}

/* Customizing feature component as per needs */
.jairg__form-feature .jairg__features-container__feature {
    margin: 0;
}

/* Customizing feature component as per needs */
.jairg__form-feature .jairg__features-container_feature-text {
    max-width: 700px;
}

.jairg__form-heading {
    align-items: center;
    cursor: default;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 2rem;
    width: 50%;
}

.jairg__form-heading h1 {
    font-family: var(--font-family);
    font-size: 34px;
    font-weight: 800;
    line-height: 45px;
    max-width: 510px;
    text-align: start;
}

.jairg__form-heading p {
    color: var(--color-text);
    cursor: pointer;
    font-family: var(--font-family);
    font-size: .5rem;
    font-weight: 500;
    line-height: 1rem;
}

.jairg__form-container {
    cursor: default;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    margin: 2rem;
    width: 50%;
}

/* Customizing feature component as per needs */
.jairg__form-container .jairg__features-container__feature {
    display: unset;
    flex-direction: column;
    flex: 1;
    margin: 1rem 0;
    min-width: 210px;
}

/* Customizing feature component as per needs */
.jairg__form-container .jairg__features-container_feature-text {
    margin-top: 0.5rem;
}

.file-input {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
}

.file-input .MuiOutlinedInput-input {
    cursor: default;
}

.submit-btn-container {
    align-items: center;
    display: flex;
    justify-content: flex-end;
    width: 100%;
}

@media screen and (max-width: 850px) {
    .jairg__form {
        align-items: center;
        flex-direction: column;
        justify-content: center;
        padding: 1rem;
    }

    .jairg__form-container {
        align-items: center;
        justify-content: center;
        margin: 2rem 1rem;
        width: auto;
    }

    .jairg__form-heading {
        align-items: flex-start;
        flex-direction: column;
        justify-content: center;
        width: auto;
    }
    
    .jairg__form-heading h1 {
        text-align: center;
    }

    .jairg__form-heading p {
        margin-top: 1rem;
    }

    .agtc-img {
        width: 90%;
    }
}

@media screen and (max-width: 650px) {
    /* Customizing feature component as per needs */
    .jairg__form-feature .jairg__features-container__feature {
        flex-direction: column;
    }

    /* Customizing feature component as per needs */
    .jairg__form-feature .jairg__features-container_feature-text {
        margin-top: 0.5rem;
    }

    .jairg__form-heading h1 {
        font-size: 28px;
        line-height: 40px;
    }
}

@media screen and (max-width: 350px) {
    /* Customizing feature component as per needs */
    .jairg__form-container .jairg__features-container__feature {
        margin: 1rem 0;
        min-width: 100%;
    }
}
