.countdown-wrapper {
  align-items: center;
  cursor: default;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  width: 100%;
}

.text {
  color: var(--color-text);
  font-size: 1.5rem;
  font-weight: bold;
  padding: .2rem;
  text-align: center;
}

.value {
  font-size: 2.8rem;
  font-weight: bold;
  padding: .2rem;
}

.stop-wrapper {
  align-items: center;
  display: flex;
  justify-content: space-between;
  margin-top: .4rem;
  width: 100%;
}
