.jairg__contributors {
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
}

.jairg__contribution-banner-container {
    align-items: flex-start;
    display: flex;
    flex-direction: column;
    flex: 1;
    justify-content: flex-start;
    margin-right: 5rem;
    text-align: left;
}

.jairg__contributors-container {
    align-items: center;
    display: flex;
    flex-direction: column;
    flex: 1.5;
    justify-content: flex-start;
}

.contribution-banner {
    width: 100%;
}

@media screen and (max-width: 990px) {
    .jairg__contributors {
        flex-direction: column;
    }

    .jairg__contribution-banner-container {
        margin: 0 0 2rem 0;
    }
}
