.jairg__features-container__feature {
    align-items: flex-start;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 1rem 0;
    width: 100%;
}

.jairg__features-container__feature-title {
    flex: 1;
    margin-right: 2rem;
    max-width: 100%;
}

.jairg__features-container__feature-title h1 {
    color: #fff;
    cursor: default;
    font-family: var(--font-family);
    font-size: 18px;
    font-weight: 800;
    letter-spacing: -0.04em;
    line-height: 24px;
}

.jairg__features-container__feature-title div {
    background: var(--gradient-bar);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    height: 3px;
    margin-bottom: 0.25rem;
    width: 16%;
}

.jairg__features-container_feature-text {
    cursor: default;
    display: flex;
    flex: 2;
    max-width: 96%;
}

.jairg__features-container_feature-text p {
    color: var(--color-text);
    font-family: var(--font-family);
    font-size: 14px;
    font-weight: 500;
    line-height: 24px;
}

.profile-link {
    display: inline-block;
    transition: .3s ease-in-out;
}

.profile-link:hover {
    color: #71E5FF;
    transform: translateY(-0.05rem);
}

@media screen and (max-width: 550px) {
    .jairg__features-container__feature-title h1 {
        font-size: 14px;
        line-height: 22px;
    }

    .jairg__features-container_feature-text p {
        font-size: 12px;
        line-height: 20px;
    }

    .jairg__features-container__feature {
        margin: 1rem 0;
    }
}