.jairg_logo_container {
  align-items: center;
  cursor: pointer;
  display: flex;
  justify-content: center;
  transition: .5s ease-in-out;
}

.jairg_logo_container:hover {
  transform: scale(1.08);
}

.jairg_logo_img {
  width: 4rem;
}

.jairg_logo {
  color: white;
  font-family: var(--font-family);
  font-size: 1.1rem;
  margin: 0 2rem 0 0;
}

.jairg_logo_container:hover .jairg_logo {
  color: #71E5FF;
}

.jairg_logo h1 {
  cursor: pointer;
  width: fit-content;
}

.jairg_logo_bottom {
  border-top: 1px solid white;
  bottom: .6rem;
  font-size: .8rem;
  margin: 0;
  position: relative;
  transition: .5s ease-in-out;
}

.jairg_logo_container:hover .jairg_logo_bottom {
  border-top: 1px solid #71E5FF;
}
